import React, { Component } from "react";
import api from "../../services/apiConstants";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import "./district.css";
const axios = require("axios");

export class District extends Component {
  constructor(props) {
    super(props);
    this.state = {
      districtList: {},
      state_name: ""
    };
  }

  componentDidMount() {
    let url = `${api.DISTRICT_LIST}`;
    let _this = this;
    axios({
      url,
      headers: {
        token:
          "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..ak3OSYopsW211SvJnzH4nNLvcj9Z4B16fR329fZbHeA"
      }
    })
      .then(function(response) {
        if (response && response.data)
          _this.setState({
            districtList: response.data.district
          });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  addState = () => {
    const { state_name } = this.state;
    let url = `${api.ADD_STATE}`;
    let _this = this;
    let data = {
      state_name: "sdddsds"
    };
    if (state_name !== "") {
      axios
        .post(url, data, {
          headers: {
            "content-type": "application/json",
            token:
              "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..ak3OSYopsW211SvJnzH4nNLvcj9Z4B16fR329fZbHeA"
          }
        })
        .then(function(response) {
          if (response && response.data)
            _this.setState({
              districtList: response.data.district
            });
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  };

  handleChange = event => {
    this.setState({
      state_name: event.target.value
    });
  };

  render() {
    const { districtList } = this.state;

    return (
      <div>
        <div className="cards-wrapper">
          {districtList &&
            districtList.length > 0 &&
            districtList.map(district => {
              return (
                <div className="state-card">
                  <div id="card-number">{district.id}</div>
                  <div id="state-name">{district.district_name}</div>
                  <FontAwesomeIcon icon={faArrowRight} />
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}

export default District;
